.feedback-form {
  max-width: 480px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  margin: 5% auto 0;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 10px;
  font-weight: 700;
  display: flex;
}

input[type="email"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px 0;
  padding: 10px;
  font-size: 16px;
}

textarea {
  width: 100%;
  resize: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px 0;
  padding: 10px;
  font-family: inherit;
  font-size: 16px;
}

input[type="email"]:focus-visible, textarea:focus-visible {
  border: 1px solid #3a10e5;
  outline: 1px solid #3a10e5;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #3a10e5;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 700;
  transition: background-color .2s ease-in;
}

button[type="submit"]:hover {
  background-color: #53f;
}

